
<template>
  <el-container>
    <el-header style="text-align: right; font-size: 12px"
      ><el-menu
        :default-active="activeIndex"
        class="el-menu-demo"
        mode="horizontal"
        @select="handleSelect"
      >
        <el-menu-item index="1"
          ><div @click="handleMenuCollapse" class="toggle-icon">
            <i
              :class="collapsed ? 'el-icon-s-unfold' : 'el-icon-s-fold'"
            ></i></div
        ></el-menu-item>

        <el-menu-item index="2"
          ><router-link to="/public">Home</router-link></el-menu-item
        >
        <el-menu-item index="3" v-if="showAdminBoard"
          ><router-link to="/board-admin">
            <!-- {{ roleUser.toString() }} -->
            Admin Board
          </router-link>
        </el-menu-item>
        <el-menu-item index="3" v-if="showModeratorBoard"
          ><router-link to="/board-mod">
            <!-- {{ roleUser.toString() }} -->
            Mod Board
          </router-link>
        </el-menu-item>
        <el-menu-item index="4"
          ><router-link to="/board-user"> User Board</router-link>
        </el-menu-item>

        <el-menu-item index="5" v-if="!currentUser" style="margin-left: 507px"
          ><router-link to="/register">Register</router-link></el-menu-item
        >
        <el-menu-item index="6" v-if="!currentUser"
          ><router-link to="/login">Login</router-link></el-menu-item
        >

        <el-menu-item index="5" style="margin-left: 507px" v-if="currentUser">
          <i class="el-icon-user"></i>
          <router-link to="/profile">
            {{ currentUser.username }} profile</router-link
          ></el-menu-item
        >
        <el-menu-item index="6" v-if="currentUser"
          ><i class="el-icon-right"></i
          ><a @click.prevent="logOut">LogOut</a></el-menu-item
        >
      </el-menu>
    </el-header>
    <Main />
  </el-container>
</template>
<script>
import Main from "../views/Main";
export default {
  name: "TopBar",
  components: {
    Main,
  },
  props: {
    collapsed: Boolean,
  },
  data() {
    return {
      activeIndex: "1",
      activeIndex2: "1",
    };
  },
  methods: {
    handleMenuCollapse() {
      this.$emit("handleMenuCollapse");
    },
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    },
    logOut() {
      this.$store.dispatch("auth/LOGOUT");
      this.$router.push("/login");
    },
  },
  computed: {
    currentUser() {
      return this.$store.state.auth.user;
    },
    showAdminBoard() {
      if (this.currentUser && this.currentUser["roles"]) {
        return this.currentUser["roles"].includes("ROLE_ADMIN");
      }

      return false;
    },
    showModeratorBoard() {
      if (this.currentUser && this.currentUser["roles"]) {
        return this.currentUser["roles"].includes("ROLE_MODERATOR");
      }

      return false;
    },
    roleUser() {
      return this.$store.state.auth.user.roles;
    },
  },
  emits: ["handleMenuCollapse"],
};
</script>

<style scoped>
.el-aside {
  color: #333;
}
.side-menu {
  width: 200px;
}
.el-header {
  color: #333;
  line-height: 60px;
}

.el-menu--collapse {
  width: 60px;
}
.el-menu-item * {
  text-decoration: none;
}
</style>

