import { createRouter, createWebHistory } from "vue-router";
import Home from "../views/HomeMain.vue";
import Login from "../views/Login.vue";
import Register from "../views/Register.vue";

const Profile = () => import("../views/Profile.vue");
const BoardAdmin = () => import("../views/BoardAdmin.vue");
const BoardModerator = () => import("../views/BoardModerator.vue");
const BoardUser = () => import("../views/BoardUser.vue");
const Public = () => import("../views/BoardPublic.vue");

const routes = [
  {
    path: "/",
    component: Home,
    children: [
      {
        path: "/profile",
        name: "profile",
        components: { Profile: Profile },
      },
      {
        path: "/public",
        name: "public",
        components: { Public: Public },
      },
    ],
  },
  {
    path: "/home",
    name: "home",
    component: Home,
  },
  { path: "/login", name: "login", component: Login },
  {
    path: "/register",
    component: Register,
  },
  {
    path: "/board-admin",
    name: "boardAuthorizated",
    component: BoardAdmin,
  },
  {
    path: "/board-mod",
    name: "moderator",
    component: BoardModerator,
  },
  {
    path: "/board-user",
    name: "user",
    component: BoardUser,
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
