<template >
  <!-- <el-container> -->
  <div class="card-login">
    <el-card class="box-card">
      <el-alert
        v-if="message"
        title="error alert"
        type="error"
        :value="message"
      >
      </el-alert>
      <template #header>
        <div class="card-header">
          <span>Login Form</span>
        </div>
      </template>

      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        status-icon
        :label-position="labelPosition"
        label-width="100px"
      >
        <el-form-item label="Username" prop="username">
          <el-input v-model="ruleForm.username" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="Password" prop="password">
          <el-input
            type="password"
            v-model="ruleForm.password"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <div class="btn-login-form">
            <el-button
              to="/"
              type="success"
              :disabled="loading"
              plain
              @click.prevent="login"
            >
              <span
                v-show="loading"
                class="spinner-border spinner-border-sm"
              ></span
              >Login</el-button
            >
            <router-link to="/signup"
              ><el-button type="danger" plain> Sign Up</el-button></router-link
            >
          </div>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
  <!-- </el-container> -->
</template>

<script>
export default {
  name: "Login",
  data() {
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("Please input the password"));
      } else {
        // if (this.ruleForm.checkPass !== "") {
        //   this.$refs.ruleForm.validateField("checkPass");
        // }
        // Password is not correct
        callback();
      }
    };
    return {
      loading: false,
      message: "",
      labelPosition: "top",
      ruleForm: {
        username: "",
        password: "",
      },
      rules: {
        username: [
          {
            required: true,
            message: "Please input your email",
            trigger: "blur",
          },
        ],
        password: [
          {
            required: true,
            validator: validatePass,
            trigger: "blur",
          },
        ],
      },
      // methods: {

      // },
    };
  },
  computed: {
    loggedIn() {
      return this.$store.state.auth.status.loggedIn;
    },
  },
  created() {
    if (this.loggedIn) {
      this.$router.push("/profile");
    }
  },
  methods: {
    login() {
      this.loading = true;

      this.$store
        .dispatch("auth/LOGIN", {
          username: this.username,
          password: this.password,
        })
        .then(
          () => {
            this.$router.push("/profile");
          },
          (error) => {
            this.loading = false;
            this.message =
              (error.response &&
                error.response.data &&
                error.response.data.message) ||
              error.message ||
              error.toString();
          }
        );
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          console.log("submit!");
          this.$router.push("/");
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
  },
};
</script>


